.App {
  text-align: center;
  display:flex;
  flex-direction: column;
  justify-content:space-evenly;
}

.adminBut{
  position: relative;
  top:0vh;
 
  
}

.divide {
  width:95vw;
  height:.5vh;
  background-color: rgb(243, 235, 224)
}
.info{

}
.sub{
  display:none;
}
.songcard{
  color:rgb(253, 244, 231);
  grid-template-columns: 1fr 1fr 1fr;
  background-color: #010e13;
  font-size: .9em;
  width:90%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column-reverse;
  margin:auto;
margin-bottom:7px;
border: 1px solid gray;
border-radius: 5px;
}

.songgrid{
  display: flex;
  grid-template-columns:  1fr 1fr ;
  background-color: #224b55;
  font-size: .7em;
  width:75%;
  grid-template-columns: 12em repeat(1fr, 1fr,1fr) 100px;
  margin:auto;
margin-bottom:6px;
flex-direction: column-reverse;

grid-auto-flow: row;

}
.logo{
  height:60vh;
}
.spanX {
  cursor: pointer;
  font-size:22px;
}

button {
  background-color:#3f7280;
  color:antiquewhite;
  height:30px;
  width:120px;
  border-radius: 10px;
}
input {
  height:25px;
  width:170px;
  padding:5px;
  padding-left: 67px;
}
textarea {
  height:35px;
  width:200px;
  padding:5px;
  border:12,12,blue;
}
label {
 
}
.djnotes {
  background-color: black;
  color:antiquewhite;
  width:7vw;
  border-radius: 5px;
  margin-left: 1px;
margin-bottom: 10px;
 
}
.check {
  padding: 2px;
  display:block;
  position:relative;
  top:3vh;
  left:1vw;
  border-radius: 5px;
 
}